import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';
import Dialog from '@mui/material/Dialog';
import DialogContent from '@mui/material/DialogContent';
import CircularProgress from '@mui/material/CircularProgress';
import Stack from '@mui/material/Stack';

function SaveDialog({ open }) {
  console.log('save dialog', open);
  return (
    <Dialog open={open}>
      <DialogContent
        sx={{
          alignItems: 'center',
        }}
      >
        <Stack
          direction="row"
          justifyContent="space-between"
          alignItems="center"
          spacing={4}
        >
          <CircularProgress sx={{ mr: 2 }} />
          Saving your work locally
        </Stack>
      </DialogContent>
    </Dialog>
  );
}

SaveDialog.propTypes = {
  open: PropTypes.bool.isRequired,
};

export default function Save({ children, handleSave }) {
  const [saved, setSaved] = useState(false);
  const [open, setOpen] = useState(true);

  console.log('save locally', open, saved);
  useEffect(async () => {
    await handleSave();
    setSaved(true);
    setOpen(false);
  }, []);

  if (!saved) {
    return <SaveDialog open={open} />;
  }

  if (saved) return children;
}

Save.propTypes = {
  children: PropTypes.node.isRequired,
  handleSave: PropTypes.func.isRequired,
};
